<template>
  <div>
    <!--    @click:row='rowgodetail' -->
    <mrc_ess_table v-if="$route.name=='valueIntconfig'"
                   :headers='headers'
                   hideAddBtn
                   :slotKey="['table_description']"
                   :paginationOptions='options'
                   @turnPage='getlist'
                   :isLoading='loading'
                   :items='desserts'>
      <template v-slot:top>
        搜索框
      </template>
      <!-- 表格 -->
      <template v-slot:item.table_description='{item}'>
        <v-chip v-for="(item1,index1) in item.table_description"
                :key="index1">
          {{item1}}
        </v-chip>
      </template>

      <!-- 按钮 -->
      <template v-slot:item.actions='{item}'>
        <menu-list :list='menulist'
                   @handleClick='handleClick($event,item)'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
      </template>
    </mrc_ess_table>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { valuelist } from '@/service/api/internationalization.js'

export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,
      options: {
        pagesize: 5,//条数
        cpage: this.$store.state.cpage,  //页数
        totalCount: null, //总条数
        totalpage: null, //总页数
      },
      desserts: [],
    };
  },
  created () { },
  async mounted () {
    await this.getlist()
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    rowgodetail (item) {
      console.log(item);
      this.$store.commit('set_iconstatus', true)  //行内进图标显示
      // this.$store.commit('set_uuid', item.uuid)  //
      this.$store.commit('set_subPage_name', item.module_name)  //
      this.$router.push({
        name: 'detailvalueIntconfig',
        params: { title: item.module_name },
      })
    },
    handleClick ({ index }, item) {
      switch (index) {
        case 0:
          this.rowgodetail(item)
          break
        case 1:
          this.$store.commit('set_iconstatus', false)  //行内进图标显示
          // this.$store.commit('set_uuid', item.uuid)  //
          this.$store.commit('set_subPage_name', item.module_name)  //
          this.$router.push({
            name: 'detailvalueIntconfig',
            params: { title: item.module_name }
          })
          break
      }
    },
    goadd () {
      this.$store.commit('set_iconstatus', '')  //行内进图标显示
      this.$store.commit('set_uuid', '')  //
      this.$store.commit('set_subPage_name', '')  //
      this.$router.push({
        name: 'addpromptmanagement'
      })
    },
    async getlist (obj) {
      this.loading = true
      if (this.desserts.length == 1 && this.options.cpage != 1) this.options.cpage--
      if (obj) {
        const { cpage, pagesize } = obj
        this.options = obj
      }
      const res = await valuelist({
        cpage: this.options.cpage,
        pagesize: this.options.pagesize
      })
      if (res.code == 200) {
        this.options.totalCount = res.totalCount
        this.options.totalpage = res.totalpage
        this.desserts = res.data.map(item => {
          if (item.table_description) {
            item.table_description = (item.table_description.indexOf(',') != -1) ? item.table_description.split(',') : [item.table_description];
            return item
          }
          else {
            return item
          }
        })
      }
      this.loading = false
      // this.desserts = this.desserts.map(item => {
      //   item.table_description = (item.table_description.indexOf(',') != -1) ? item.table_description.split(',') : [item.table_description];
      //   return item
      // })
    }
  },
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_The_module'),
          align: 'start',
          width: '300',
          sortable: false,
          value: 'module_name'
        },
        {
          text: this.$t('label_form'),
          align: 'start',
          sortable: false,
          value: 'table_description'
        },
        {
          text: '',
          width: '50',
          value: 'actions',
          sortable: false,
          align: 'center',
        }
      ]
    },
    menulist () {
      return [
        { name: this.$t('label_View') },
        { name: this.$t('label_Edit') },
      ]
    }
  },
  watch: {
    $route (v) {
      if (v.name == '/valueIntconfig') {
        this.getlist()
      }
    }
  },
};
</script>

